import { Component, OnInit, Input, HostListener } from '@angular/core';
import { Film } from 'src/app/interfaces/interfaces';
import { ModalController } from '@ionic/angular';
import { DetailComponent } from '../detail/detail.component';
import { Router } from '@angular/router';


const screenWidthBreakPoint: number = 720;
const screenWidthBreakPointMid: number = 420;

@Component({
  selector: 'app-slideshow-big',
  templateUrl: './slideshow-big.component.html',
  styleUrls: ['./slideshow-big.component.scss'],
})
export class SlideshowBigComponent implements OnInit {

  @Input() films: Film[];

  slideRecentOpts: any = {};

  screenWidth: number;

  constructor(private modalCtrl: ModalController,
              private router: Router) { }

  ngOnInit() {
      this.getScreenSize();
  }

  async showDetails(id: string, title: string, name: string): Promise<void>{
    let style = '';
    let _name = ''
    if(name != undefined){
      style = 'tv'
      _name = name
    }
    if(title != undefined){
      style = 'movie'
      _name = title
    }
    this.router.navigate([`/app/details/${id}/${style}/${_name}`])
  }


  @HostListener('window:resize', ['$event'])
  getScreenSize(event?) {
        this.screenWidth = window.innerWidth;
        if (this.screenWidth > screenWidthBreakPoint){
          this.slideRecentOpts = {
            slidesPerView: 5.5,
            centeredSlides: true,
            centeredSlidesBounds: true
          }
        } else if (this.screenWidth > screenWidthBreakPointMid) {
          this.slideRecentOpts = {
            slidesPerView: 2.3,
            centeredSlides: true,
            centeredSlidesBounds: true
          }
        } else {
          this.slideRecentOpts = {
            slidesPerView: 1.5,
            centeredSlides: true,
            centeredSlidesBounds: true
          }
        }
    }

}
