import { Component, OnInit, Input, HostListener, Output, EventEmitter } from '@angular/core';
import { Film } from 'src/app/interfaces/interfaces';
import { ModalController } from '@ionic/angular';
import { DetailComponent } from '../detail/detail.component';
import { Router } from '@angular/router';

const screenWidthBreakPoint: number = 720;
const screenWidthBreakPointMid: number = 420;


@Component({
  selector: 'app-slideshow-pares',
  templateUrl: './slideshow-pares.component.html',
  styleUrls: ['./slideshow-pares.component.scss'],
})
export class SlideshowParesComponent implements OnInit {

    @Input() films: Film[] = [];
    @Output() loadMore = new EventEmitter();

    slideRecentOpts: any = {};

    screenWidth: number;

    constructor(private modalCtrl: ModalController,
                private router: Router) { }

    ngOnInit() {
        this.getScreenSize();
        this.films.filter(film => {
          if(film.poster_path){
            return true
          }else if(film.backdrop_path){
            return true
          } else {
            return false
          }
        });
    }

    async showDetails(id: string, title: string, name: string): Promise<void>{
      let style = '';
      let _name = ''
      if(name != undefined){
        style = 'tv'
        _name = name
      }
      if(title != undefined){
        style = 'movie'
        _name = title
      }
      this.router.navigate([`/app/details/${id}/${style}/${_name}`])
    }


    load(): void {
      this.loadMore.emit();
    }

    @HostListener('window:resize', ['$event'])
    getScreenSize(event?) {
          this.screenWidth = window.innerWidth;
          if (this.screenWidth > screenWidthBreakPoint){
            this.slideRecentOpts = {
              slidesPerView: 9.5,
              centeredSlides: true,
              centeredSlidesBounds: true,
              spaceBetween: -10
            }
          } else if (this.screenWidth > screenWidthBreakPointMid) {
            this.slideRecentOpts = {
              slidesPerView: 4.3,
              centeredSlides: true,
              centeredSlidesBounds: true,
              spaceBetween: -10
            }
          } else {
            this.slideRecentOpts = {
              slidesPerView: 2.3,
              centeredSlides: true,
              centeredSlidesBounds: true,
              spaceBetween: -10
            }
          }
      }

}
