import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/auth/auth.service';
import { LoginPage } from 'src/app/auth/pages/login/login.page';
import { LoginOptionsPage } from 'src/app/auth/pages/login-options/login-options.page';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss'],
})
export class NavbarComponent implements OnInit {

  logoUrl: string = "../../assets/logo.svg";

  auth: boolean = false;

  userInitials: string = '';

  constructor( private authService: AuthService,
    private modalController: ModalController) { }

  ngOnInit() {
    this.isAuth()
  }

  isAuth() {
    this.authService.whoAmI().subscribe(res => {
      this.auth = true
      if(this.auth){
        this.userInitials = this.authService.usuario.firstName[0]+this.authService.usuario.lastName[0]
      }
    }, err => {
        this.userInitials = '';
    })
  }

  async login() {
    const loginOptionsModal = await this.modalController.create({
      component: LoginPage,
    });
    loginOptionsModal.onDidDismiss().then(data => {this.isAuth()})
    return await loginOptionsModal.present();
  }

  async loginOptions(){
    const loginModal = await this.modalController.create({
      component: LoginOptionsPage,
    });
    loginModal.onDidDismiss().then(data => {this.isAuth()})
    return await loginModal.present();
  }
}
