import { Component, OnInit } from '@angular/core';
import { ModalController, AlertController } from '@ionic/angular';
import { AuthService } from '../../auth.service';

@Component({
  selector: 'app-login-options',
  templateUrl: './login-options.page.html',
  styleUrls: ['./login-options.page.scss'],
})
export class LoginOptionsPage implements OnInit {


    logoUrl: string = "../../assets/cool-user.png";

  constructor(
    private modalController: ModalController,
    public authService: AuthService,
    public alertController: AlertController) { }

  ngOnInit() {
  }

  logout(){
    console.log("logout")
    this.authService.logout();
    this.presentAlert("Se ha cerrado la sesión");
  }

  async presentAlert(msg: string) {
    this.dismissLoginOptions();
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: msg,
      buttons: ['OK']
    });

    await alert.present();
  }

  dismissLoginOptions() {
    this.modalController.dismiss();
  }
}
